"use client"
import { usePathname, useSearchParams } from "next/navigation"
import { useRouter } from "next/navigation";
import "./Filter.scss"  

 const Filter = ({}) => {
    const pathname = usePathname();
    const searchParams = useSearchParams();
    const{ replace } = useRouter();

    const selectFilterChange =((e)=>{
        const {name,value} = e.target;
        const params = new URLSearchParams(searchParams);
        params.set(name,value);
        replace(`${pathname}?${params.toString()}`)
    })

  return (
    <div className='filter_area'>
        <div className="filter_item">
            <select className="drop_down" name="category" onChange={selectFilterChange}>
                <option>Category</option>
                <option value="All Products">All Products</option>
                <option value="boys">boys</option>
                <option value="Featured Products">Featured Products</option>
                <option value="Girls">Girls</option>
                <option value="New Born">New Born</option>
            </select>

            

        </div>
        <div className="filter_item">
            <input type="text" name="min" placeholder="Min Price" onChange={selectFilterChange}></input>
        </div>
        <div className="filter_item">
            <input type="text" name="max" placeholder="Max Price" onChange={selectFilterChange}></input>
        </div>
        <div className="filter_item">
        <select className="drop_down" name="sort" onChange={selectFilterChange}>
            <option>Sort By</option>
            <option value="asc_price">Price (low to high)</option>
            <option value="desc_price">Price (high to low)</option>
            <option value="asc_lastUpdated">Newest</option>
            <option value="desc_lastUpdated">Oldest</option>
            </select>

        </div>
    </div>
  )
}

export default Filter