const PRODUCT_PER_PAGE = 10; // Default value if not provided

const ProductList = ({ categoryId, limit = PRODUCT_PER_PAGE, searchParams }) => {
  const [products, setProducts] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    const fetchProducts = async () => {
      setLoading(true);
      try {
        const wixClient = await wixClientServer();

        let productQuery = wixClient.products
          .queryProducts()
          .startsWith("name", searchParams?.name || "")
          .eq("collectionIds", categoryId)
          .hasSome(
            "productType",
            searchParams?.type ? [searchParams.type] : ["physical", "digital"]
          )
          .gt("priceData.price", searchParams?.min || 0)
          .lt("priceData.price", searchParams?.max || 999999)
          .limit(limit)
          .skip(
            searchParams?.page
              ? parseInt(searchParams.page) * limit
              : 0
          );

        if (searchParams?.sort) {
          const [sortType, sortBy] = searchParams.sort.split(" ");
          if (sortBy) {
            if (sortType === "asc") {
              productQuery.ascending(sortBy);
            } else if (sortType === "desc") {
              productQuery.descending(sortBy);
            }
          }
        }

        const res = await productQuery.find();
        setProducts(res.items || []);
      } catch (err) {
        setError(err.message || "Failed to load products.");
      } finally {
        setLoading(false);
      }
    };

    fetchProducts();
  }, [categoryId, limit, searchParams]);

  if (loading) return <div>Loading products...</div>;
  if (error) return <div>Error: {error}</div>;

        return (
            <div>
            <div className="product_page">
                <div className="container">
                    <div className='inner_'>
                        <ul className="product_list">
                            {res.items.map((product,index)=>{
                                return(
                                <li className="" key={index}>
                                    <Link href={"/" + product.slug}>
                                    <div className='img_wrap'>
                                        <img src={product.media?.items[1]?.image?.url} />
                                    </div>
                                    <div className="name_price">
                                    <label className='cat_name'>{product.name}</label>  
                                    <label className='cat_price'>${product.price?.price}</label>  
                                    </div>
                                    <button className="add_cart">Add to Cart</button>
                                    </Link>
                                </li>
                                )
                            })}
                        </ul>
                        </div>
                </div>
            </div>
            </div>
        );
    }

    export default ProductList